export default {
	data() {
		return {
			flag: 0,
			teacherInfo: {
				id: '',
				phone: ''
			}
		};
	},
	methods: {
		handleBind() {
			let that = this
			console.log('绑定');
			console.log(this.teacherInfo);
			uni.setStorageSync('INFO', this.teacherInfo)
			if (!this.flag) {
				this.flag ++
				uni.request({
					url: 'http://127.0.0.1:8888/getTeacherId',
					data: {
						id: that.teacherInfo.id
					},
					success(res) {
						if (res.data.data) {
							let data = res.data.data
							uni.request({
								url: 'http://127.0.0.1:8888/teacherBind',
								method: 'POST',
								data: {
									...that.teacherInfo,
									openid: uni.getStorageSync('OPENID'),
									avatarUrl: uni.getStorageSync('USERINFO').avatarUrl,
									name: data.name
								},
								success() {
									uni.showToast({
										title: '等待审核',
										duration: 1000,
										success() {
											uni.navigateTo({
												url: '/pages/init/init'
											})
										}
									})
								},
								fail() {
									uni.showToast({
										title: '绑定失败',
										duration: 1000
									})
									that.flag = 0
								}
							})
						} else {
							uni.showToast({
								title: '未找到该老师',
								duration: 1000
							})
							that.flag = 0
						}
					}
				})
			}
			// uni.navigateTo({
			// 	url: '../bindChild/bindChild?childCount=' + this.childCount
			// });
		},
		idValue(value) {
			this.teacherInfo.id = value
		},
		phoneValue(value) {
			this.teacherInfo.phone = value
		}
	}
};
